body, html {
  margin: 0;
  padding: 0;
}

img, svg {
  height: inherit;
  width: inherit;
}

body, input {
  font-family: 'Work Sans', sans-serif !important;
  font-weight: 300 !important;
 }
 
 h1, h2, h3, h4, h5, h6 {
  font-family: 'Montserrat', sans-serif !important;
  font-weight: 900 !important;
 }

 input:focus{
   outline: none;
 }